body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main-info-container {
  display: flex;
  color: green;
  font-weight: bolder;
  font-size: 1em;
  margin-bottom: 50px;
}

.main-info-element {
  margin: 5px;
}

.main-info-container span {
  overflow: hidden;
  border-right: .15em solid green;
  padding-right: 20px;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: .25em;
  -webkit-animation: 
    typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite;
          animation: 
    typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite;
}

/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: green; }
}
@keyframes blink-caret {
  from, to { border-color: transparent }
  50% { border-color: green; }
}
.cmd {
    background-color: #282c34;
    color: green;
    font-weight: bolder;
    font-size: 1em;
    outline: none;
    border:none;
}

.cmd-window-hidden {
    visibility: hidden;
}

.cmd-window {
    visibility: visible;
    margin-top: 20px;
    border: 1px green solid;
    width: 500px;
    height: 500px;
    background-color: black;
    -webkit-animation: fade 1s linear;
            animation: fade 1s linear;
}

.cmd-window-element {
    margin: 0;
    padding: 0;
    overflow: hidden;
    display: flex;
    padding-left: 20px;
    width: 90%;
    float: left;
    height: 38px;
    word-break: break-all;
    color: green;
    font-size: 1em;
    font-weight: 500;
}

/* The typing effect */
@-webkit-keyframes fade {
    0% { height: 0px }
    100% { height: 500px }
}
@keyframes fade {
    0% { height: 0px }
    100% { height: 500px }
}
