.cmd {
    background-color: #282c34;
    color: green;
    font-weight: bolder;
    font-size: 1em;
    outline: none;
    border:none;
}

.cmd-window-hidden {
    visibility: hidden;
}

.cmd-window {
    visibility: visible;
    margin-top: 20px;
    border: 1px green solid;
    width: 500px;
    height: 500px;
    background-color: black;
    animation: fade 1s linear;
}

.cmd-window-element {
    margin: 0;
    padding: 0;
    overflow: hidden;
    display: flex;
    padding-left: 20px;
    width: 90%;
    float: left;
    height: 38px;
    word-break: break-all;
    color: green;
    font-size: 1em;
    font-weight: 500;
}

/* The typing effect */
@keyframes fade {
    0% { height: 0px }
    100% { height: 500px }
}